.main {
  width: 610px;
  padding: 30px;
  margin: 30px;
  border: 1px soild black;
}

section {
  margin-bottom:24px;
}

h1{
  margin:0px;
  padding:0px;
  font-size:36px;
  font-weight: bold;
  font-style: italic;
  margin-bottom: 8px;
  text-align: right;
}

h4{
  margin:0px;
  padding:0px;
  font-size:20px;
  font-weight: bold;
  font-style: italic;
  text-align: right;
}

h2{
  margin:0px;
  padding:0px;
  font-size:20px;
  color:#1e90ff;
  font-weight: 400;
  margin-bottom: 16px;
  border-bottom: 1px solid #3d3c3c;
  padding-bottom: 5px;
}
.myphoto{
  width: 130px;
  height: 130px;
  border-radius: 10%;
  float: left;
}

.title{
  font-size:16px;
  color:#7c7c7c;
  font-weight: bold;
  float: right;
  line-height: 16px;
}

.sns-img{
  text-align: left;
  width: 30px;
  height: 30px;
}

.text{
  line-height: 16px;
  font-size: 15px;
  font-weight:bold;
  color:black;
  margin-bottom:6px;
}

.righttext{
  font-size:11px;
  font-weight: 300;
  color: #282828;
  float: right;
}